// need it for generator function
import 'babel-polyfill';
import React from 'react';
import ReactDOM from 'react-dom';
import { applyMiddleware, createStore } from 'redux';
import { Provider } from 'react-redux';
import createSagaMiddleware from 'redux-saga';
import thunk from 'redux-thunk';
//
import Style from 'main.scss';
import { NAV } from 'router/config';
import rootRouter from 'router/rootRouter';
import rootReducer from 'model/reducer/rootReducer';
import rootSaga from 'model/saga/rootSaga';

const sagaMiddleware = createSagaMiddleware();
const middlewares = [sagaMiddleware, thunk];
if (process.env.NODE_ENV === 'development') {
  const { logger } = require('redux-logger');
  middlewares.push(logger);
}
//
const store = createStore(rootReducer, applyMiddleware(...middlewares));
sagaMiddleware.run(rootSaga);

//
ReactDOM.render(<Provider store={store}>{rootRouter(NAV.PAGE_HOME.key)}</Provider>, document.getElementById('app'));
