import { createAction } from 'redux-actions';

// async sub-actions
export const REQUEST = 'REQUEST';
export const SKIPPED = 'SKIPPED';
export const SUCCESS = 'SUCCESS';
export const FAILURE = 'FAILURE';
//
let id = 1e6;

// default meta
const initMetaCreator = persist => () => ({ id: id++, async: true, type: REQUEST, persist });
const createMetaCreator = type => (payload, meta) => (meta ? { ...meta, type } : undefined);

const createAsyncAction = (actionPrefix, shouldPersist = false) => {
  const initialActionCreator = createAction(`${actionPrefix}_${REQUEST}`, undefined, initMetaCreator(shouldPersist));
  const actionBundle = Object.assign(initialActionCreator, {
    skipped: createAction(`${actionPrefix}_${SKIPPED}`, undefined, createMetaCreator(SKIPPED)),
    success: createAction(`${actionPrefix}_${SUCCESS}`, undefined, createMetaCreator(SUCCESS)),
    failure: createAction(`${actionPrefix}_${FAILURE}`, undefined, createMetaCreator(FAILURE))
  });
  return actionBundle;
};

export default createAsyncAction;
