import _ from 'lodash';

// event type
export const RESIZE_TYPE = 'resize';

const domEventMap = {
  [RESIZE_TYPE]: new Map()
};

let resizeEventHandle = null;

const registerEvents = (type, subscriberMap) => {
  switch (type) {
    case RESIZE_TYPE:
      if (resizeEventHandle === null) {
        resizeEventHandle = _.debounce(
          () =>
            subscriberMap.forEach((value, key) => {
              key && key.apply(value[0], [RESIZE_TYPE, ...value.slice(1)]);
            }),
          100,
          { trailing: true }
        );
        window.addEventListener(RESIZE_TYPE, resizeEventHandle);
      }
      break;
  }
};

const unregisterEvents = type => {
  switch (type) {
    case RESIZE_TYPE:
      if (resizeEventHandle) {
        window.removeEventListener(RESIZE_TYPE, resizeEventHandle);
        resizeEventHandle = null;
      }
      break;
  }
};

export const subscribeEvent = (type, callback, context = null, ...args) => {
  const map = domEventMap[type];
  if (map) {
    // use map.set to avoid multiple subscribe for a same callback
    map.set(callback, [context, ...args]);
    // only add once
    if (map.size === 1) {
      registerEvents(type, map);
    }
  }
};

// TODO: unsubscribe all callbacks under a same type
export const unsubscribeEvent = (type, callback) => {
  const map = domEventMap[type];
  if (map && map.has(callback)) {
    map.delete(callback);
    // only remove the event if all subscribers deleted
    if (map.size === 0) {
      unregisterEvents(type);
    }
  }
};
