// gtag('config', 'GA_TRACKING_ID', {'page_path': '/new-page.html'});
// Google Analytics
export const ga = {};
ga.trackingId = 'UA-127347266-1';

ga.track = page => {
  let path = page;
  if (process.env.NODE_ENV === 'development') {
    path = '/dev' + path;
  }
  window.gtag && window.gtag('config', ga.trackingId, { page_path: path });
};
